.Loader_ldsSpinner__3nEB5 {
  color: official;
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
  align-self: center;
  margin: 100px 0;
}
.Loader_ldsSpinner__3nEB5 div {
  transform-origin: 40px 40px;
  animation: Loader_lds-spinner__2XqYM 1.2s linear infinite;
}
.Loader_ldsSpinner__3nEB5 div:after {
  content: " ";
  display: block;
  position: absolute;
  top: 3px;
  left: 37px;
  width: 6px;
  height: 18px;
  border-radius: 20%;
  background: #fff;
}
.Loader_ldsSpinner__3nEB5 div:nth-child(1) {
  transform: rotate(0deg);
  animation-delay: -1.1s;
}
.Loader_ldsSpinner__3nEB5 div:nth-child(2) {
  transform: rotate(30deg);
  animation-delay: -1s;
}
.Loader_ldsSpinner__3nEB5 div:nth-child(3) {
  transform: rotate(60deg);
  animation-delay: -0.9s;
}
.Loader_ldsSpinner__3nEB5 div:nth-child(4) {
  transform: rotate(90deg);
  animation-delay: -0.8s;
}
.Loader_ldsSpinner__3nEB5 div:nth-child(5) {
  transform: rotate(120deg);
  animation-delay: -0.7s;
}
.Loader_ldsSpinner__3nEB5 div:nth-child(6) {
  transform: rotate(150deg);
  animation-delay: -0.6s;
}
.Loader_ldsSpinner__3nEB5 div:nth-child(7) {
  transform: rotate(180deg);
  animation-delay: -0.5s;
}
.Loader_ldsSpinner__3nEB5 div:nth-child(8) {
  transform: rotate(210deg);
  animation-delay: -0.4s;
}
.Loader_ldsSpinner__3nEB5 div:nth-child(9) {
  transform: rotate(240deg);
  animation-delay: -0.3s;
}
.Loader_ldsSpinner__3nEB5 div:nth-child(10) {
  transform: rotate(270deg);
  animation-delay: -0.2s;
}
.Loader_ldsSpinner__3nEB5 div:nth-child(11) {
  transform: rotate(300deg);
  animation-delay: -0.1s;
}
.Loader_ldsSpinner__3nEB5 div:nth-child(12) {
  transform: rotate(330deg);
  animation-delay: 0s;
}
@keyframes Loader_lds-spinner__2XqYM {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

